.page6 {

    --page-header-height: 4rem;
    
    --page-nav-height: 3rem;
    
    --page-menu-width: 100%;
    --page-menu-color: #111;
    --page-menu-bgcolor: #EEE;
    
    --page-aside-width: 100%;
    --page-aside-color: #111;
    --page-aside-bgcolor: #EEE;
    
    --page-footer-height: 3rem;

    height: 100%;
}

.page6.document {
    background-color: #FFFFFF;
    background-color: var(--paper-color);
    border: solid 1px rgba(100,100,100,.3);
    border: solid 1px var(--divider-color);
    margin: 20px;
}

.page6>menu {
    padding: 0px;
    margin: 0px;
    grid-area: menu;
    width: var(--page-menu-width);
    color: var(--page-menu-color);
    background-color: var(--page-menu-bgcolor);
}

.page6>header {
    grid-area: header;
    height: var(--page-header-height);
}

.page6>nav {
    grid-area: nav;
    height: var(--page-nav-height);
}

.page6>main {
    grid-area: main;
    overflow: hidden;
}

.page6>aside {
    grid-area: aside;
    width: var(--page-aside-width);
    color: var(--page-aside-color);
    background-color: var(--page-aside-bgcolor);
}

.page6>footer {
    grid-area: footer;
    min-height: var(--page-footer-height);
}

:root {
    --timeout: 800ms;
}

.workspace { 
    display: grid;
    grid-template-areas: "header header header" "nav nav nav" "menu main aside" "menu detail aside" "footer footer footer";
    grid-template-columns: auto 1fr auto;
    grid-template-rows: auto auto 1fr auto;
}

@media (max-width: 415px) {

    .workspace {
        display: block;
        position: relative;
        background-color: rgb(68, 58, 87);
    }

    .workspace>* {
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: white;
    }

}

.shift-enter {
    top: 15%;
    left: 5%;
    max-width: 90%;
    opacity: .5;
    z-index: 20;
    border-radius: 20px;
}

.shift-enter-active {
    top: 0px;
    left: 0px;
    max-width: 100%;
    opacity: 1;
    border-radius: 0;
    transition: all 800ms;
    transition: all var(--timeout);
}

.shift-enter-done {
    z-index: 30;
}

.shift-exit {
    border: solid 1px black;
    top: 0px;
    left: 0px;
    max-width: 100%;
    opacity: 1;
    border-radius: 0;
}

.shift-exit-active {
    top: 5%;
    left: 10%;
    max-width: 80%;
    opacity: .6;
    border-radius: 10px;
    transition: all 800ms;
    transition: all var(--timeout);
}

