.site6 {
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-columns: auto 1fr auto;
    grid-template-rows: 3rem 1fr;
    grid-template-rows: var(--site-header-height) 1fr;
    grid-template-areas: "nav header header" "nav main aside";
    background-color: #f4f7f9;
    background-color: var(--background-color);
}

.site6>header {
    grid-area: header;
    color: #FFF;
    color: var(--site-header-color);
    background-color: #e3e3e3;
    background-color: var(--site-header-bgcolor);
}

.site6>header>i {
    display: none;
}

.site6>nav {
    grid-area: nav;
    overflow: hidden;
    color: #FFF;
    color: var(--site-drawer-title-color);
    background-color: #353535;
    background-color: var(--site-drawer-bgcolor);
    display: flex;
    flex-direction: column;
}

.site6>nav.min {
    width: 3.4rem;
    width: var(--site-drawer-min);
}

.site6>nav.max {
    width: 14rem;
    width: var(--site-drawer-max);
}

.site6>nav>header {
    height: 3rem;
    height: var(--site-header-height);
    padding: 0 1rem;
    display: flex;
    align-items: center;
    border-bottom: solid 1px rgba(100,100,100,.3);
    border-bottom: solid 1px var(--divider-color);
    font-weight: 500;
    font-size: 1.2rem;
}

.site6>nav>header>.logo {
    height: 1.8rem;
    margin-right: 1rem;
}

.site6>nav>main {
    flex: 1 1;
    overflow-x: hidden;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
}

.site6>nav>main .mdc-list-item, .site6>nav>main .mdc-list-item__graphic {
    height: 32px;
    font-weight: 500;
    font-size: 14px;
    color: rgba(255,255,255,.7) !important;
    color: var(--site-drawer-color) !important;
    margin: 0;
    padding: 1rem;
}

.site6>nav>main .mdc-list-item__meta {
    color: rgba(255,255,255,.7) !important;
    color: var(--site-drawer-color) !important;
}

.site6>nav>footer {
    border-top: solid 1px rgba(100,100,100,.3);
    border-top: solid 1px var(--divider-color);
    padding: 0;
}

.site6>nav.min .mdc-icon-button {
    border-left: solid 3px transparent;
}

.site6>nav.min .mdc-icon-button--on {
    border-left: solid 3px #c8cfd8;
    border-left: solid 3px var(--site-drawer-select-color);
    background-color: rgba(171, 150, 245, 0.2);
}

.site6>nav.min .section-divider {
    margin: 0 5px;
    border-top: solid 1px rgba(255,255,255,.3);
    border-bottom: solid 1px rgba(255,255,255,.2);
}

.site6>main {
    grid-area: main;
    overflow: hidden;
}

.site6>aside {
    grid-area: aside;
}

.site6>.site-preview {
    position: absolute;
    z-index: 10;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, .32);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

@media (max-width: 415px) {
    .site6 {
        grid-template-areas: "header" "main";
        grid-template-columns: 1fr;
    }
    .site6>header>i {
        display: block;
    }
    .site6>nav {
        display: none;
        position: absolute;
        top: 0px;
        bottom: 0px;
        z-index: 20;
    }
    .site6>nav.show {
        display: flex;
    }

    /** CSSTransitions **/

    .drawer-enter {
        opacity: 0;
        left: -4rem;
    }
    .drawer-enter-active {
        opacity: 1;
        left: 0rem !important;
        transition: all 1500ms;
        transition: all var(--drawer-timeout);
    }
    .drawer-enter-done {
        left: 0rem !important;
    }
    .drawer-exit {
        opacity: 1;
    }
    .drawer-exit-active {
        opacity: 0;
        left: -4rem !important;
        transition: all 1500ms;
        transition: all var(--drawer-timeout);
    }

}
