.quantity {
    flex: 1 1;
    position: relative;
    height: 6rem;
    overflow: hidden;
}

.quantity>main {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 1rem;
}

.quantity>main .text-field,
.quantity>main button {
    z-index: 5;
    height: 3rem;
}

.quantity>footer label  {
    min-width: 64px;
    text-align: center;
    font-size: .8rem;
    font-style: italic;
    color: var(--secondary-text-color);
}

.quantity>footer {
    position: absolute;
    top: 35px;
    width: 100%;
    display: flex;
    align-items: center;
    height: 5rem;
}
.form-header {
    padding: 0 1rem;
}

.form-grid {
    display: grid;
    grid-auto-rows: 4.2rem;
    grid-gap: 1rem;
}

.field-wrapper {
    display: flex;
}

.field-wrapper>* {
    flex: 1 1;
}

.backdrop {
    background-color: #f4f7f9;
    background-color: var(--background-color);
    height: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}

.backdrop>details {
    min-height: 4rem;
    overflow: auto;
}

.backdrop>details[open] {
    flex: 1 1;
}

.backdrop>details>summary {
    background-color: #f4f7f9;
    background-color: var(--background-color);
    position: -webkit-sticky;
    position: sticky;
    top: 0px;
    z-index: 5;
    min-height: 4rem;
}

.backdrop>details>main {
    padding: 0 1rem;
}

.backdrop>article {
    flex: 1 1;
    min-height: 4rem;
    background-color: #FFFFFF;
    background-color: var(--paper-color);
    border-radius: 20px 20px 0 0;
    border-top: solid 1px rgba(100,100,100,.3);
    border-top: solid 1px var(--divider-color);
    display: flex;
    flex-direction: column;
}

.backdrop>article>header {
    padding: 1.4rem 2rem 1rem 2rem;
}

.backdrop>article>main {
    flex: 1 1;
    overflow: auto;
}
.wizard {
    box-shadow: 0 2px 2px 0 rgba(0,0,0,.14),
      0 3px 1px -2px rgba(0,0,0,.2),
      0 1px 5px 0 rgba(0,0,0,.12);
    box-shadow: var(--shadow1);
    margin: 2rem;
}

.wizard-navigation {
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-shadow: 0px -3px 5px 0px rgba(150,150,150,0.3);
    box-shadow: var(--shadow-top);
    padding: 0 1rem;
}

.wizard-navigation>.dots {
    display: flex;
    align-items: center;
    justify-content: center;
}

.dots>.dot {
    height: 1rem;
    width: 1rem;
    border-radius: 1rem;
    border: solid 2px rgba(100,100,100,.3);
    border: solid 2px var(--divider-color);
    background-color: #FFFFFF;
    background-color: var(--paper-color);
    margin: 0 .5rem;
}

.dots>.dot.selected {
    background-color: rgba(100,100,100,.3);
    background-color: var(--divider-color);
}

.wizard-breadcrumb-bar {
    height: 3rem;
    padding: 1rem;
    display: flex;
    align-items: center;
}

.wizard-breadcrumb-bar>.breadcrumb {
    font-size: 1rem;
    color: rgba(100,100,100,.3);
    color: var(--divider-color);
    text-transform: capitalize;
}

.wizard-breadcrumb-bar>.breadcrumb.selected {
    color: black;
    font-weight: 500;
}

.wizard-breadcrumb-bar>.breadcrumb>.divider {
    padding: 0 1rem;
    color: red;
}

.wizard-breadcrumb-bar>.breadcrumb:last-of-type>.divider {
    display: none;
}
.kanban {
