    color: rgb(72, 39, 133);
    color: var(--primary-color-light);
}

.kanban-card {
    margin: 2rem;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,.14),
      0 3px 1px -2px rgba(0,0,0,.2),
      0 1px 5px 0 rgba(0,0,0,.12);
    box-shadow: var(--shadow1);
    background-color: #FFFFFF;
    background-color: var(--paper-color);
    border-radius: 5px;
    min-height: 10rem;
}

.kanban-card.red {
    border-left: solid 5px red;
}

.secondary-text {
    color: rgba(100, 100, 100, .8);
}
.login-box {
    background-color: #FFFFFF;
    background-color: var(--paper-color);
    display: grid;
    grid-template-areas:
        "header header" 
        "main main"
        "footer footer";
    animation: fadeIn 2s;
}

.login-box>header {
    grid-area: header
}

.login-box>header {
    display: flex;
    flex: 1 1;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}
  
.login-box>header>img {
    max-width: 90%;
}
  
.login-box>header>.title {
    font-size: 2rem;
    font-weight: 600;
}

.login-box>main {
    grid-area: main;

    padding: 1rem;

    display: flex;
    flex-direction: column;
}

.login-box>main>* {
    margin: .5rem 0;
}

.login-box>footer {
    grid-area: footer;
    padding: 0 1rem 1rem 1rem;
    display: flex;
    justify-content: flex-end;
}

@keyframes fadeIn {
    from { opacity: 0; }
    to   { opacity: 1; }
}
.viewer {
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, .75);
    display: grid;
    grid-template-columns: 1fr auto;
    grid-template-rows: 4rem 1fr;
    grid-template-areas: "header aside" "main aside";
    color: #FFF;
}

.viewer>header {
    grid-area: header;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.65) 0%, transparent 100%);
    background-image: linear-gradient(rgba(0, 0, 0, 0.65) 0%, transparent 100%);
    background-position-x: 0;
    background-position-x: initial;
    background-position-y: 0;
    background-position-y: initial;
    background-size: auto auto;
    background-size: initial;
    background-repeat-x: initial;
    background-repeat-y: initial;
    background-attachment: scroll;
    background-attachment: initial;
    background-origin: padding-box;
    background-origin: initial;
    background-clip: border-box;
    background-clip: initial;
    background-color: transparent;
    background-color: initial;
}

.viewer>main {
    grid-area: main;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: auto;
}

.viewer>main>.resizer {
    width: 100%;
    overflow: hidden;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
}

.viewer>main>.resizer img {
    width: 100%;
    object-fit: fill;
}

@media (min-width: 800px) {
    .viewer>main>.resizer {
        width: 800px;
        padding: 0;
    }
}

.viewer>aside {
    min-width: 22rem;
    grid-area: aside;
    display: none;
    background-color: rgb(50, 50, 50);
}

.viewer>aside.open {
    grid-area: aside;
    display: flex;
    flex-direction: column;
}

.viewer>aside .property>label {
    color: #AAA;
}

.viewer>footer {
    width: 15rem;
    position: absolute;
    bottom: 1rem;
    left: calc(50vw - 4rem);
    border-radius: 3px;
    z-index: 100;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: .5rem;
    background-color: rgba(0, 0, 0, .75);
}
.curtains {
    width: 100%;
    height: 50rem;
    display: flex;
}

.curtain {
    border: solid 1px rgba(100,100,100,.3);
    border: solid 1px var(--divider-color);
    height: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    width: 5rem;
}

.curtain>main {
    flex: 1 1;
}

.curtain.open {
    flex: 1 1;
    width: 100%;
}

.curtain.closed {
    min-width: 5rem;
}

.curtain>footer {
    padding: 1rem;
    display: flex;
    justify-content: space-between;
}

/** CSSTransitions **/

.courtain1-enter {
    min-width: 5rem;
}

.courtain1-enter-active {
    width: 100%;
    transition: all 1000ms;
}

.curtain1-exit {
    width: 80%;
}

.curtain1-exit-active {
    width: 5rem;
    transition: all 1000ms ease;
}

.curtain1-exit-done {
    width: 5rem;
}

